import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';

var Table = require('react-bootstrap/lib/Table');
var Button = require('react-bootstrap/lib/Button');

class Entry extends React.Component {
  render() {
    return (
      <Button className={"entry" + (this.props.value === 1 ? " selected-button" : "")} onClick={() => this.props.onClick()}>
        {this.props.value}
      </Button>
    );
  }
}

class TableView extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      sizeX: 20,
      sizeY: 10,
      data: Array.from(Array(20).fill(0), () => new Array(10).fill(0)),
    };

    this.kingsAlgoStep = this.kingsAlgoStep.bind(this);
  }

  handleClick(i,j) {
    const newState = this.state.data.slice();
    newState[i][j] = newState[i][j] === 0 ? 1 : 0;
    this.setState({data: newState});
  }

  prodsumDiff(a,b){
    return (
      b.reduceRight((accumulator, currentValue, index, arr) => accumulator + currentValue * Math.pow(2,arr.length - index)) -
      a.reduceRight((accumulator, currentValue, index, arr) => accumulator + currentValue * Math.pow(2,arr.length - index))
    )
  }

  kingsAlgoStep(isCol){
    const dataClone = this.state.data.slice();

    if(isCol){
      const sorted = dataClone.sort(this.prodsumDiff);

      this.setState({data: sorted});
    } else {
      const transpose = dataClone.reduce((prev, next) => next.map((v, i) => (prev[i] || []).concat(next[i])), []);
      const sorted = transpose.sort(this.prodsumDiff);
      const transposeBack = sorted.reduce((prev, next) => next.map((v, i) => (prev[i] || []).concat(next[i])), []);
      
      this.setState({data: transposeBack});
    }
  }

  createRow(j){
    return Array(this.state.sizeX).fill().map((v,i) => <td key={"D" + i.toString() + j.toString()}><Entry value={this.state.data[i][j]} onClick={() => this.handleClick(i,j)}/></td>); 
  }

  render() {
    const heads = Array(this.state.sizeX).fill().map((v,i) => <th key={"H" + i}>{i}</th>);
    const rows = Array(this.state.sizeY).fill().map((v,i) => <tr key={"R" + i}>{this.createRow(i)}</tr>);

    return (
      <div>
        <Table>
          <thead>
            <tr>
              {heads}
            </tr>
          </thead>
          <tbody>
            {rows}
          </tbody>
        </Table>
      <Button onClick={() => this.kingsAlgoStep(true)}>Run Columns</Button>
    <Button onClick={() => this.kingsAlgoStep(false)}>Run Rows</Button>
  </div>
    );
  }
}

class Chalan extends React.Component {
  render() {
    return (
      <div className="tableview">
        <div className="table">
          <TableView />
        </div>
        <div className="input">
        </div>
      </div>
    );
  }
}

// ========================================

ReactDOM.render(
  <Chalan />,
  document.getElementById('root')
);




